import React from 'react';
import './index.css';
import './mainpg.css';

class MainPg extends React.Component {
   
    componentDidMount() {
        let maxims = [
            "Ei incumbit probatio, qui dicit, non qui negat",
            "Pacta sunt servanda",
            "Hominum causa omne ius constitutum sit",
            "Ignorantia iuris nocet, ignorantia facti non nocet",
            "Impossibilium nulla obligatio est",
            "Iustitia est constans et perpetua voluntas ius suum cuique tribuendi",
            "Lex retro non agit",
            "Scire leges non hoc est verba earum tenere, sed vim ac potestatem",
            "Vim vi repellere licet"
        ]
    setInterval(function () {
        let maximDiv = document.getElementById('chosenMaxim')
        let min = 0;
        let max = (maxims.length - 1)
        let num = Math.floor(Math.random() * (max - min));
        maximDiv.innerHTML = maxims[num]
    }, 7000)
    }

    render() {
        return (
            <div id='mainpgDiv'>
                <div className='col-4 pharmMain'>dd
                </div>
                <div className='col-4 maxim'>
                    <h1><i className="fa fa-quote-right" aria-hidden="true"></i></h1>
                    <h2 id='chosenMaxim'>Ei incumbit probatio, qui dicit, non qui negat</h2>
                    <h1><i className="fa fa-quote-right" aria-hidden="true"></i></h1>
                </div>
                <div className='col-4 kato'>gg
                </div>
            </div>
            )
    }
}

export default MainPg